@import url('https://fonts.googleapis.com/css2?family=Allerta+Stencil&family=Dekko&family=Righteous&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@300;400;500;600;700;800;900;1000&family=Righteous&display=swap');




*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navbar{
  display: flex;
  background-color: rgba(0, 0, 0,0.8);
  opacity: 0.8;
  height: 60px;
  align-items: center;
  font-family: 'Dekko', cursive;
}

ul{
  /* gap: 35px; */
  display: flex;
}

ul li{
  display: flex;
  cursor: pointer;
}

#logo{
  float: left;
  color: white;
  display: flex;
  margin-left: 5rem;
  align-items: center;
  cursor: pointer;
}

#logo span{
  font-size: 30px;
}

#logo i{
  font-size: 20px;
  color: white;
  margin-right: 15px;
}

#link_tags{
  display: flex;
  color: white;
  align-items: center;
  float: right;
  /* margin-right: 40px; */
  margin-left: 900px;
  gap: 60px;
  font-size: 25px;
}

#link_tags a{
  color: white;
}

#home{
  color: blue;
}

#user_box{
  margin-left: 5.5rem;
  color: white;
  margin-top: 0.6rem;
  font-size: 1.3rem;
  text-transform: uppercase;
}